@import url('https://fonts.googleapis.com/css2?family=Sen:wght@400;700&display=swap');
.App {
  color: black;
  font-family: Sen, 'Sen', sans-serif;
  display: flex;
  flex-direction: column;
  background-color: rgb(245, 244, 244);
}

html, body, #root, .App {
  height: 100vh;
}

html {
  overflow-y: scroll;
  scrollbar-width: 0;
}

.App-link {
  color: #09d3ac;
}

.spacer {
  width: 20px;
}

.body {
  background-color: rgb(245, 244, 244);
}

