@import url('https://fonts.googleapis.com/css2?family=Sen:wght@400;700&display=swap');

.settings-box {
	height: 250px;
	width: 500px;
	border: 1px;
  background-color: white;
	border-style: solid #D0D0D0;
	margin-top: 10px;
	margin-bottom: 10px;
	border-radius: 5px;
  color: rgb(112, 112, 112);
  font-family: Sen, 'Sen',  sans-serif;
  border-radius: 3px;
  padding: 15px;
  box-shadow: 1px 1px 10px rgb(189, 189, 189);
}

.settings-page {
  margin-top: 50px;
  font-family: Sen, 'Sen', sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.col-lg-4 {
  display: flex;
  flex-direction: row;
  margin-bottom: 5px;
}

.row {
  display: flex;
  flex-direction: column;
}

.settings-info {
  margin-top: 20px;
  width: 400px;
  margin-left: 5px;
  display: flex;
  flex-direction: column;
}

.settings-section {
  width: 200px;
  height: 200px;
	margin-top: 15px;
  display: flex;
  flex-direction: column;
	justify-content: center;
  border-right: solid 2px #888;
  margin-right: 20px;
  padding-left: 25px;
  padding-bottom: 20px;
}

.settings-section-button {
  margin-top: 2px;
  text-decoration: none;
  font-size: 15px;
  color: white;
  display: flex;
  justify-content: center;
  text-align: center;
  line-height: 30px;
  transition: all .2s ease-in-out;
  border-radius: 5px;
  width: 100px;
  background-color: #b0ce9a;
  border-style: none;
  outline: none;
}

.settings-section-button:hover {
	color: white;
  background: #789c61;
  outline: none;
}

.settings-section-selected {
  margin-top: 2px;
  text-decoration: none;
  font-size: 15px;
  color: white;
  display: flex;
  justify-content: center;
  text-align: center;
  line-height: 30px;
  transition: all .2s ease-in-out;
  border-radius: 5px;
  width: 100px;
  background-color: #789c61;
  border-style: none;
  outline: none; 
}

.user-settings {
  flex-direction: row;
  width: 100%;
  margin-right: 10px;
  margin-top: 20px;
  font-size: 10pt;
  align-items: flex-end;
}

.user-settings input {
  font-size: 10pt;
  border: none;
  border-bottom: 1px solid #ccc;
  outline: none;
  background: transparent;
  z-index: 2;
  top: 0;
  margin-left: 5px;
  width: 55%;
  float: right;
}

.user-settings label {
  top: 20px;
  font-size: 12pt;
  z-index: 1;
  color: #999;
}
