@import url('https://fonts.googleapis.com/css2?family=Sen:wght@400;700&display=swap');

.dd-wrapper {
  font-family: Sen, 'Sen', sans-serif;
  user-select: none;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
  width: 200px;
  height: 45px; 
  border: 1px solid rgb(64, 95, 91);
  border-radius: 3px;
}

.cron-dd-wrapper {
  height: 30px;
  width: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1.4px solid rgb(64, 95, 91);
  border-radius: 3px;
  font-size: 14px;
  margin-right: 3px;
}

.dd-header {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 38px;
  background-color: rgb(255, 255, 255);
  cursor: default;
  color: rgb(64, 95, 91);
}

.cron-dd-header { 
  height: 100%;
  width: 100%;
  line-height: 16px;
}

.dd-header-selected {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 38px;
  background-color: rgb(255, 255, 255);
  cursor: default;
  position: relative;
  margin-bottom: 5px;
  color: rgb(64, 95, 91);
}

.cron-dd-header-selected { 
  height: 100%;
  width: 100%;
  margin: 0;
  cursor: pointer;
}


.dd-header-selected:after {
  content: '\00bb';
  transform: rotate(90deg);
  transition: .2s linear;
}

.cron-dd-header-selected:after {
  content: none;
}

.dd-header:after {
    content: '\00bb';
}

.cron-dd-header:after {
  content: none;
}

.dd-header:hover {
  cursor: pointer;
}

.dd-header:focus:after {
  transform: rotate(90deg);
}

.dd-header-title {
  font-weight: 300;
  margin: 2px 20px;
}

.cron-dd-header-title {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  justify-content: center;
  font-size: 14px;
}

.inactive {
  opacity: 0;
  height: 0;
  padding: 0;
  margin: 0;
}

.dd-list {
  padding-inline-start: 0px;
  z-index: 10;
  position: absolute;
  width: 100%;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(64, 95, 91);
  top: 28px;
  max-height: 215px;
  overflow-y: scroll;
  scrollbar-width: 0;
  -webkit-overflow-scrolling: touch;
  transition: height .5s linear;
  height: auto;
  max-height: 400px;
  color: rgb(74, 95, 64);
  right: -1px;
}

.cron-dd-list {
  top: 16.4px;
  width: 100%;
  right: -1.6px;
  border: 1.4px solid rgb(64, 95, 91);
}

.dd-list-item {
  width: 95%;
  font-size: 1rem;
  padding-top: 7px;
  padding-bottom: 7px;
  line-height: 1rem;
  cursor: default;
  display: inline-block;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-left: 5px;
  padding-right: 5px;
  transition: font-size .3s linear;
  color: rgb(64, 95, 91);
}

.cron-dd-list-item {
  font-size: 14px;
}

.selected {
  color: rgb(64, 95, 91);
  font-size: 1.1rem;
}

.dd-list-item:hover {
  cursor: pointer;
  color: rgb(64, 95, 91);
  transition: font-size .3s linear;
  font-size: 1.1em;
}

.cron-dd-list-item:hover {
  font-size: 16px;
}

::-webkit-scrollbar {
    width: 0px;
    height: 0px;
}
 
::-webkit-scrollbar-track {
    -webkit-border-radius: 10px;
    border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background:lightgrey; 
}
::-webkit-scrollbar-thumb:window-inactive {
    background: lightgrey; 
  background: lightgrey; 
    background: lightgrey; 
  background: lightgrey; 
    background: lightgrey; 
}