@import url('https://fonts.googleapis.com/css2?family=Sen:wght@400;700&display=swap');
.homepage {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    font-family: Sen, 'Sen', sans-serif;
    align-items: flex-start;
    flex: 0;
    width: 100%;
    margin-top: 50px;
    /*background: url(../images/leaves.png) repeat 0 0;
    background-attachment: fixed;*/
}

.tile-container {
    max-width: 800px;
    height: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    font-family: Sen, 'Sen', sans-serif;
    align-items: center;
    flex: 0;
    width: 100%;
}

.max-width {
    margin-top: 10px;
    max-width: 90%;
}

.tile {
    margin: 1.5%;
    margin-top: 2%;
    padding: 3%;
    width: 250px;
    height: 300px;
    color:#9fb68e;
    background: #c5d8b9;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    position: relative;
    font-weight: 700;
    transition: background-color ease-in-out .5s, color ease-in-out .5s, box-shadow ease-in-out .5s;
    box-shadow: 1px 1px 10px lightgrey;
}

.medium-tile {
    margin-top: 5px;
    width: 150px;
    height: 200px;
}

.small-tile {
    margin-top: 5px;
    width: 100px;
    height: 75px;
}

.tile:hover {
    background-color: #b0ce9a;
    color: white;
    box-shadow: 2px 2px 5px lightgrey;
    transition: ease-in-out .5, color ease-in-out .5s;
    cursor: pointer;
}

.tile:hover img {
    filter: none;
    -webkit-filter: grayscale(0%);
    opacity: 1;
    transition: ease-in-out .5s, -webkit-filter ease-in-out .5s;
}

.tile:active {
    background-color: #89ad70;
}

.tile img {
    margin-top: 40px;
    filter: grey;
    -webkit-filter: grayscale(100%);
    opacity: .5;
    transition: ease-in-out .5s, -webkit-filter ease-in-out .5s;
}

.tile-name-large {
    text-align: center;
    width: auto;
    margin-top: 10px;
    font-size: 34px;
}

.tile-name-medium {
    text-align: center;
    width: auto;
    margin-top: 10px;
    font-size: 28px;
}

.tile-name-small {
    text-align: center;
    width: auto;
    margin-top: 10px;
    font-size: 22px;
}

.selected-tile .tile-name {
    margin-top: 10px;
    font-size: 40px;
}

.selected-tile {
    color: rgb(64, 95, 91);
    width: 100%;
    height: auto;
    z-index: 2;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: top;
    top: 51px;
}

.back-button {
    position: absolute;
    margin-left: 10px;
    margin-right: 5px;
    margin-top: 1px;
    font-size: 40px;
    color: white;
    background-color: rgb(82, 131, 139);
    border-radius: 5px;
    cursor: pointer;
    outline: 0;
    display: flex;
    font-family: inherit;
    width: 40px;
    height: 40px;
    border-style: none;
    transition: linear .2s;
    transform: rotate(180deg);
    align-items: center;
    justify-content: center;
}

.hidden {
    visibility: collapse;
    border: .5px solid lightgrey;
    margin: 2%;
    margin-top: 5%;
    padding: 3%;
    width: auto;
    height: 00px;
    color: #b0ce9a;
    background-color: #d3eec0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 10px;
    opacity: 0;
    display: none;
}

.hidden img {
    visibility: collapse;
    margin-top: 40px;
    filter: grey;
    opacity: .5;
    -webkit-filter: grayscale(100%);
}

.back-button span:after {
    top: -8px;
    left: 8px;
    position: absolute;
    content: '\00bb';
}

.reset-button {
    position: relative;
}

.reset-button span:after {
    top: -5px;
    left: 4px;
    position: absolute;
    content: '\21BA'
}

.back-button:hover {
    background-color:rgb(121, 171, 179); 
}

.back-button:active {
    color: #c4c4c4;
    transition: .1s linear;
}


@media screen and (max-width: 540px) {

    .selected-tile .tile-name {
        margin-top: 10px;
        font-size: 30px;
    }

    .back-button {
        transform: translateY(-15px) translateX(-5px) rotate(180deg);
    }

}

