@import url('https://fonts.googleapis.com/css2?family=Sen:wght@400;700&display=swap');

.default-input {
  font-family: Sen, 'Sen', sans-serif;
  font-size: 14pt;
  line-height: 2.6em;
  padding: 0;
  margin: 0;
  position: relative;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  text-align: left;
}

.default-input div {
  display: flex;
  width: 100%;
}

.form-input {
  margin: 1px;
  color: black;
}

.default-input input {
  font-size: 14pt;
  border: none;
  outline: none;
  top: 0;
  left: 0;
  margin: 1px;
  background: transparent;
  z-index: 2;
  border-bottom: 1px solid #ccc;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  color: black;
}

.default-input label {
  font-size: 12pt;
  padding-left: 10px;
  z-index: 1;
  color: #999;
  justify-content: center;
  display: center;
  flex-direction: row;
  color: black;
}

.login-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: flex-start;
  margin-top: 100px;
  width: 400px;
  height: 500px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 1px 1px 10px lightgrey;
  justify-self: center;
  align-self: center;
  padding: 10px;
}

.login-inputs {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 70px;
}

.login-input {
  display: flex;
  flex-direction: row;
  font-family: Sen, 'Sen', sans-serif;
  font-size: 12pt;
  justify-content: left;
  align-items: right;
  text-align: right;
  width: auto;
  height: 1.8em;
  color: rgb(64, 95, 91);
  margin-bottom: 5px;
}

.signup-inputs {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.login-input input {
  font-size: 12pt;
  border: none;
  outline: none;
  margin: 1px;
  background: transparent;
  z-index: 2;
  border: 1px solid rgb(64, 95, 91);
  border-radius: 40px;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 200px;
  color: rgb(64, 95, 91);
  text-align: center;
}

.login-input input::placeholder {
  color: rgb(64, 95, 91);
}

.login-input .right-label {
  font-size: 12pt;
  padding-left: 10px;
  z-index: 1;
  color: rgb(64, 95, 91);
  justify-content: center;
  display: center;
  flex-direction: row;
}

.login-input .left-label {
  font-size: 15pt;
  padding-right: 10px;
  z-index: 1;
  color: rgb(64, 95, 91);
  justify-content: center;
  display: center;
  flex-direction: row;
}

.signup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 400px;
}

.signup-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: flex-start;
  margin-top: 100px;
  text-align: center;
  width: 400px;
  min-height: 400px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 1px 1px 10px lightgrey;
  align-self: center;
  justify-self: center;
  padding: 10px;
  padding-bottom: 20px;
}

.signup-question {
  color: rgb(78, 94, 105);
  font-size: 30px;
  padding-bottom: 20px;
}

.paypal-signup {
  display: flex;
  flex-direction: column;
}

.signup-container .default-input {
  width: auto;
}

.signup-button-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100px;
  width: 300px;
}

.signup-button {
  margin-top: 10px;
}

.logo {
  margin-right: 40px;
  height: 200px;
  width: auto;
  justify-content: left;
  transform: rotate(-10deg) translateY(20px)
}

.login-button {
  box-shadow: 2px 2px 5px lightgrey;
  width: 200px;
}

.login-button-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 130px;
  margin-top: 10px;
}

.login-input .default-input {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.password-reset-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  margin-top: 100px;
  width: 400px;
  height: 450px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 1px 1px 10px lightgrey;
  align-self: center;
  justify-self: center;
}

.password-reset-input-container {
  display: flex;
  flex-direction: column;
  justify-content: right;
  align-items: flex-end;
}

.password-reset-button-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;
  height: 100px;
}

.forgot-password-link {
  color: rgb(45, 119, 230);
  cursor: pointer;
  outline: 0;
  font-size: 10pt;
  display: inline-block;
  font-family: Sen, 'Sen', sans-serif;
  width: 150px;
  margin-left: auto;
  margin-right: auto;
  border-style: none;
  background-color: transparent;
}

.forgot-password-link:hover {
  color: rgb(45, 202, 230);
  cursor: pointer;
  outline: 0;
  font-size: 10pt;
  display: inline-block;
  font-family: Sen, 'Sen', sans-serif;
  width: 150px;
  margin-left: auto;
  margin-right: auto;
  border-style: none;
  background-color: transparent;
}

.error-message {
  align-self: center;
  font-style: Avenir, sans-serif;
  text-align: center;
}


.connect-message {
  margin-top: 15%;
  align-self: center;
  justify-self: center;
  text-align: center;
}
