@import url('https://fonts.googleapis.com/css2?family=Sen:wght@400;700&display=swap');
.Form {
  text-align: center;
  color: black;
  font-family: Sen, 'Sen', sans-serif;
}

.spacer {
  width: 20px;
}

.show-element {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 300px;
  padding-top: 100px;
  opacity: 1;
  visibility: visible;
  transition: opacity 2s linear;
}

.dont-show-element {
  visibility: hidden;
  opacity: 0;
  margin-top: -10000px;
}

.bottom-spacer {
  padding-bottom: 300px;
}

.element h1 {
  font-family: Sen, 'Sen', sans-serif;
  display: flex;
  width: 60%;
  margin-top: 10px;
  margin-right: 0;
  transform: translateX(40%);
}

.time-select {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  flex-direction: row;
  transform: translateY(70%) translateX(30%);
}

.Calendar {
  display: flex;
  padding-top: 40px;
  padding-bottom: 300px;
}

.hours-container {
  display: flex;
  flex-wrap: wrap;
  height: 50%;
  width: 300px;
  transform: translateY(10%);
}

.submit-button {
  transform: translateY(280%) translateX(-6%);
  z-index: 1;
  display: flex;
  justify-content: center;
}

