.navigation {
  list-style: none;
  background: #c5d8b9;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  z-index: 2;
  margin-right: 50px;
}

.header {
  font-family: Sen, 'Sen', sans-serif;
  list-style: none;
  margin: 0;
  background: #c5d8b9;
  height: 50px;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  justify-content: space-between;
  z-index: 999;
  position: fixed;
}

.navigation a {
  text-decoration: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 1em;
  color: rgb(134, 151, 101);
  transition: all .2s ease-in-out;
  width: 70px;
  margin-left: 20px;
  text-align: center;
  height: 0px;
  border-radius: 30px;
}

.navigation a:hover {
  background: #ffffff;
  border-radius: 30px;
}

.navigation a.active {
  background: #ffffff;
  border-radius: 30px;
}

.header-logo {
  margin-top: 0px;
  margin-left: 40px;
  height: 55px;
  width: auto;
  justify-content: left;
}

.hide-nav {
  visibility: collapse;
  opacity: 0;
}


@media screen and (max-width: 540px) {

  .header {
    font-size: 10px;
  }

  .navigation a {
    padding: 1.1em;
    margin-left: 5px;
    width: 40px;
  }

  .header-logo {
    margin-left: 10px;
    align-self: center;
    height: 30px;
  }

  .navigation {
    margin-right: 10px;
  }

}

.default-button {
  border-radius: 0.5em;
  padding: 0.5em 1em;
  background-color: #9cca7d;
  color: #fff;
  cursor: pointer;
  outline: 0;
  font-size: 14pt;
  display: inline-block;
  font-family: Sen, 'Sen', sans-serif;
  width: auto;
  border-style: none;
}

.default-button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.default-button span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.default-button:hover span {
  padding-right: 20px;
}
 

.default-button:hover span:after {
  opacity: 1;
  right: 0;
}

