    color:#9fb68e;
    background: #c5d8b9;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    position: relative;
    font-weight: 700;
    transition: background-color ease-in-out .5s, color ease-in-out .5s, box-shadow ease-in-out .5s;
    box-shadow: 1px 1px 10px lightgrey;
}

.medium-tile {
    margin-top: 5px;
    width: 150px;
