.back-button {
    position: absolute;
    margin-left: 10px;
    margin-right: 5px;
    margin-top: 1px;
    font-size: 40px;
    color: white;
    background-color: rgb(82, 131, 139);
    border-radius: 5px;
    cursor: pointer;
    outline: 0;
    display: flex;
    font-family: inherit;
    width: 40px;
    height: 40px;
    border-style: none;
    transition: linear .2s;
    transform: rotate(180deg);
    align-items: center;
    justify-content: center;
}

.hidden {
    visibility: collapse;
    border: .5px solid lightgrey;
    margin: 2%;
    margin-top: 5%;
    padding: 3%;
    width: auto;
    height: 00px;
    color: #b0ce9a;
    background-color: #d3eec0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 10px;
    opacity: 0;
    display: none;
}

.hidden img {
    visibility: collapse;
    margin-top: 40px;
    -webkit-filter: grey;
            filter: grey;
    opacity: .5;
    -webkit-filter: grayscale(100%);
}

.back-button span:after {
    top: -8px;
    left: 8px;
    position: absolute;
    content: '\00bb';
}

.reset-button {
    position: relative;
}

.reset-button span:after {
    top: -5px;
    left: 4px;
    position: absolute;
    content: '\21BA'
}

.back-button:hover {
    background-color:rgb(121, 171, 179); 
}

.back-button:active {
    color: #c4c4c4;
    transition: .1s linear;
}


@media screen and (max-width: 540px) {

