@import url('https://fonts.googleapis.com/css2?family=Sen:wght@400;700&display=swap');
.default-button {
  border-radius: 0.5em;
  padding: 0.5em 1em;
  background-color: #9cca7d;
  color: #fff;
  cursor: pointer;
  outline: 0;
  font-size: 14pt;
  display: inline-block;
  font-family: Sen, 'Sen', sans-serif;
  width: auto;
  border-style: none;
}

.default-button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.default-button span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.default-button:hover span {
  padding-right: 20px;
}
 

.default-button:hover span:after {
  opacity: 1;
  right: 0;
}

.request-button {
  font-size: 10pt;
  padding: 5px;
  display: flex-inline;
  flex-direction: column;
  margin-left: 5%;
  margin-top: 5px;
}

.request-button {
  width: 85px;
  margin-right: 10px;
}

.day-hours {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 300px;
}

.accept-button {
  background-color: #5BE24B;
}

.decline-button {
  background-color: #FF5959;
}

.remove-button {
  height: 25px;
  width: 70px;
  font-size: 10pt;
  transition: ease-in-out .5s;
  background-color: #89b16e;
  color: #fff;
  cursor: pointer;
  outline: 0;
  display: inline-block;
  font-family: inherit;
  width: auto;
  border-style: none;
  margin-left: 20px;
  border-radius: 10px;
}

.remove-button:hover {
  background-color: rgba(255, 89, 89, 0.562);
}

