.radio-button {
  margin-left: 10px;
  transition: .2s linear;
}

.label-unchecked {
  transition: linear .2s;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-self: flex-start;
  padding: 8px 15px;
  font-size: 16px;
  background-color: rgb(82, 131, 139);
  border: 2px solid rgb(64, 95, 91);
  color: white;
  border-radius: 13px;
  width: auto;
  cursor: pointer;
}

.day-of-week-label-unchecked {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  font-size: 14px;
  width: 100%;
  height: 100%;
  padding: 2px;
  margin-top: 2px;
  border-radius: 10px;
}

.label-checked {
  transition: linear .2s;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  padding: 8px 15px;
  font-size: 16;
  color: white;
  background-color: rgb(64, 95, 91);
  border: 2px solid rgb(64, 95, 91);
  border-radius: 13px;
  width: auto;
  cursor: pointer;
}

.day-of-week-label-checked {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  font-size: 14px;
  width: 100%;
  height: 100%;
  padding: 2px;
  margin-top: 2px;
  border-radius: 10px;
}

input[type="checkbox"]{
    appearance: initial;
    border: none;
    position: relative;
}

input[type="checkbox"]:checked:after {
    content: "\2713";
    color: #fff;
    position: absolute;
    transform: translateY(-3px);
}

